import { useState } from "react";

function Setup({ switchSetUp, setp1Name, setp2Name }) {
  return (
    <div className="container text-center">
      <h1>Cornhole Score Keeper</h1>
      <div className="row m-3">
        <div className="col">
          <input
            type="text"
            name="p1name"
            id="p1name"
            placeholder="Player 1 Name"
            onChange={(e) => {
              setp1Name(e.target.value);
            }}
          />
        </div>
        <div className="col">
          <input
            type="text"
            name="p2name"
            id="p2name"
            placeholder="Player 2 Name"
            onChange={(e) => {
              setp2Name(e.target.value);
            }}
          />
        </div>
      </div>
      <button
        className="btn btn-secondary"
        onClick={() => {
          switchSetUp(false);
        }}
      >
        Submit
      </button>
    </div>
  );
}

export default Setup;
