import { useState } from "react";
import Setup from "../Components/Setup";

const Home = () => {
  const [p1Score, setp1Score] = useState(0);
  const [p2Score, setp2Score] = useState(0);
  const [p1Name, setp1Name] = useState("Player 1");
  const [p2Name, setp2Name] = useState("Player 2");
  const [setUp, switchSetUp] = useState(true);

  // Score event handlers
  const single = (setScore, pScore) => {
    setScore(pScore + 1);
  };
  const double = (setScore, pScore) => {
    setScore(pScore + 2);
  };

  // HTML return
  return setUp ? (
    <Setup
      switchSetUp={switchSetUp}
      p1Name={p1Name}
      p2Name={p2Name}
      setp1Name={setp1Name}
      setp2Name={setp2Name}
    />
  ) : (
    <div className="container text-center">
      <h1 className="display-4 mb-5">Corn-Hole Score App</h1>
      <div className="row">
        <div className="col">
          <h1>{p1Name}</h1>
          <h3> {p1Score}</h3>
          <button
            className="btn btn-primary mx-2"
            onClick={() => {
              single(setp1Score, p1Score);
            }}
          >
            +1
          </button>
          <button
            className="btn btn-primary"
            onClick={() => {
              double(setp1Score, p1Score);
            }}
          >
            +2
          </button>
        </div>
        <div className="col">
          <h1>{p2Name}</h1>
          <h3> {p2Score}</h3>
          <button
            className="btn btn-primary mx-2"
            onClick={() => {
              single(setp2Score, p2Score);
            }}
          >
            +1
          </button>
          <button
            className="btn btn-primary"
            onClick={() => {
              double(setp2Score, p2Score);
            }}
          >
            +2
          </button>
        </div>
      </div>
      <button
        className="btn btn-danger"
        onClick={() => {
          setp1Score(0);
          setp2Score(0);
        }}
      >
        Reset Score
      </button>
      <button
        className="btn btn-info ml-2"
        onClick={() => {
          switchSetUp(!setUp);
        }}
      >
        Reset Game
      </button>
    </div>
  );
};

export default Home;
